import { Checkbox, FormControlLabel, FormGroup } from "@mui/material"

export default function TodoList() {
  return (
    <div>
        <FormGroup>
          <FormControlLabel
            control={<Checkbox checked={false}  />}
            label=""
          />

          {/* [2023-10-08 "Left right ] */}
          <FormControlLabel
            control={<Checkbox checked={true}  />}
            label="Left right  |  -- 2, "
          />
          <FormControlLabel
            control={<Checkbox checked={true} />}
            label="user,  | "
          />
          <FormControlLabel
            control={<Checkbox checked={false} />}
            label="radio box -- , ()"
          ></FormControlLabel>
          <FormControlLabel
            control={<Checkbox checked={false} />}
            label="BCDB "
          />
          <FormControlLabel
            control={<Checkbox checked={true} />}
            label="Todo listcheckbox | "
          />
          <FormControlLabel
            control={<Checkbox checked={true} />}
            label="APM, tab | "
          />
          <FormControlLabel
            control={<Checkbox checked={false} />}
            label=""
          />
          <FormControlLabel
            control={<Checkbox checked={false} />}
            label="PayPallogo"
          />
          <FormControlLabel
            control={<Checkbox checked={false} />}
            label="dashboardtoggle"
          />
          <FormControlLabel
            control={<Checkbox checked={false} />}
            label="user -- redux"
          />
        </FormGroup>
     
    </div>
  )
}
